.button {
  margin-top: 2em;
  width: 100%;
  position: relative;
}
.button a {
  padding: 1em 2.7em;
  position: relative;
  background-color: #5266DF;
  border-radius: 0.4em;
  color: #FFF;
  font-weight: bold;
  width: 90%;
  -webkit-box-shadow: -3px 6px 9px -3px #9b9ca1;
  -moz-box-shadow: -3px 6px 9px -3px #9b9ca1;
  box-shadow: -3px 6px 9px -3px #9b9ca1;
  text-transform: capitalize;
  margin: 3em 0;
  transition: 1s;
}

.contenidoHeader {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.contenidoHeader .img {
  display: flex;
  width: 50%;
}
@media (min-width: 1024px) {
  .contenidoHeader .img {
    padding-left: 10em;
  }
}
.contenidoHeader .mobile-menu {
  z-index: 4;
}
@media (min-width: 1024px) {
  .contenidoHeader .mobile-menu {
    display: none;
  }
}
.contenidoHeader .menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  padding-top: 7em;
  transition: 1s;
  visibility: hidden;
  opacity: 0;
}
@media (min-width: 1024px) {
  .contenidoHeader .menu {
    height: 3em;
    flex-direction: row;
    visibility: visible;
    opacity: 1;
    padding-top: 0;
    position: initial;
    width: 50%;
  }
}
.contenidoHeader .menu li {
  border-bottom: 1px solid #FFF;
  width: 85%;
  text-align: center;
  transition: 1s;
  font-weight: bold;
  font-size: 1em;
}
@media (min-width: 1024px) {
  .contenidoHeader .menu li {
    width: initial;
    border-bottom: none;
    margin-left: 5em;
  }
}
.contenidoHeader .menu li:nth-child(1) {
  border-top: 1px solid #FFF;
}
@media (min-width: 1024px) {
  .contenidoHeader .menu li:nth-child(1) {
    border-top: none;
  }
}
.contenidoHeader .menu li:nth-child(4) {
  border-bottom: 0;
  width: 100%;
  margin-top: 2em;
  margin-bottom: 5em;
}
@media (min-width: 1024px) {
  .contenidoHeader .menu li:nth-child(4) {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 5em;
    width: initial;
  }
}
.contenidoHeader .menu li:nth-child(4) a {
  padding: 1em 8em;
  background-color: transparent;
  border-radius: 0.4em;
  color: #FFF;
  border: 2px solid #FFF;
  font-weight: bold;
  width: 100%;
  text-transform: capitalize;
  margin: 3em 0;
  transition: 1s;
}
@media (min-width: 1024px) {
  .contenidoHeader .menu li:nth-child(4) a {
    color: #FFF;
    padding: 1em 3em;
    margin: 0;
    background: #ff5657;
    border: 2px solid #ff5657;
  }
  .contenidoHeader .menu li:nth-child(4) a:hover {
    color: #ff5657;
    border: 2px solid #ff5657;
    background: transparent;
  }
}
.contenidoHeader .menu li a {
  color: #FFF;
  text-transform: uppercase;
  line-height: 4em;
}
@media (min-width: 1024px) {
  .contenidoHeader .menu li a {
    color: #9B9CA1;
    width: auto;
    transition: 0.5s;
  }
  .contenidoHeader .menu li a:hover {
    color: #ff5657;
  }
}
.contenidoHeader .menu .social {
  display: flex;
  align-items: flex-end;
  margin-top: 20%;
}
@media (min-width: 1024px) {
  .contenidoHeader .menu .social {
    display: none;
  }
}
.contenidoHeader .menu .social li {
  border: none;
  margin: 0 2em;
}
.contenidoHeader .menu.mostrar {
  visibility: visible;
  opacity: 1;
}

.sup {
  display: flex;
  justify-content: space-between;
  padding: 2em 1.5em;
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  transition: 1s;
}
@media (min-width: 1024px) {
  .sup {
    width: 100%;
  }
}

.headerAnimate {
  transform: translateY(-20%);
  background-color: rgba(82, 102, 223, 0.05);
  padding-bottom: 0.8em;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: 100vh;
  width: 100%;
  background-color: rgba(36, 41, 70, 0.9);
  z-index: 1;
}

.menu.mostrar .sup {
  background: #9B9CA1;
}

.hero {
  max-width: 100%;
  height: 95vh;
  margin-top: 10em;
}
@media (min-width: 1024px) {
  .hero {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    margin-top: 3em;
    align-items: center;
  }
}

.imgHero {
  width: 100%;
  position: relative;
}
@media (min-width: 1024px) {
  .imgHero {
    width: 50%;
  }
}
.imgHero::after {
  content: "";
  position: absolute;
  width: 85%;
  height: 80%;
  background-color: #5266DF;
  bottom: 0;
  border-radius: 40% 0 0 30%;
  right: 0;
  z-index: -1;
}
.imgHero img {
  width: 100%;
}

.infoHero {
  padding-top: 1.5em;
  width: 100%;
  height: 45vh;
  text-align: center;
  padding: 0 0.2em;
  text-transform: capitalize;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .infoHero {
    width: 50%;
  }
}
@media (min-width: 1024px) {
  .infoHero .titulo {
    text-align: start;
    width: 70%;
    font-size: 2.6em;
    margin-bottom: 0.5em;
  }
}
@media (min-width: 1024px) {
  .infoHero .descripcion {
    text-align: start;
    font-size: 1.3em;
    width: 70%;
  }
}

.titulo {
  font-size: 2.2em;
  text-transform: capitalize;
  line-height: 1.2;
  color: #242A42;
}
@media (min-width: 1024px) {
  .titulo {
    font-size: 2.6em;
  }
}

.descripcion {
  color: #9B9CA1;
  line-height: 1.5;
  font-size: 1em;
  font-weight: 500;
  text-transform: none;
  margin-bottom: 2em;
}
@media (min-width: 1024px) {
  .descripcion {
    font-size: 1.3em;
  }
}

.botones {
  display: flex;
  width: 100%;
  align-items: center;
}
@media (min-width: 1024px) {
  .botones {
    width: 70%;
    align-items: center;
  }
}
.botones .button {
  width: 50%;
  display: flex;
  justify-content: center;
  margin-top: 0;
}
@media (min-width: 1024px) {
  .botones .button {
    width: 33.3%;
    margin-right: 2em;
  }
}
.botones .button a {
  display: block;
  width: 90%;
  margin-top: 0;
  font-size: 0.7em;
}
@media (min-width: 1024px) {
  .botones .button a {
    font-size: 1em;
    width: 100%;
  }
}
.botones .button:nth-child(2) a {
  background-color: rgba(155, 156, 161, 0.1);
  color: #242A42;
}

.features {
  width: 100%;
  text-align: center;
  text-transform: capitalize;
  padding: 0 0.4em;
  margin-top: 4em;
}
.features h4 {
  margin-top: 0.2em;
}
@media (min-width: 1024px) {
  .features {
    text-align: center;
  }
}

@media (min-width: 1024px) {
  .infoFeatures {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .infoFeatures .titulo {
    padding-bottom: 0.2em;
  }
}
@media (min-width: 1024px) {
  .infoFeatures .descripcion {
    margin-top: 0;
    width: 50%;
  }
}

.centro {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}
@media (min-width: 1024px) {
  .centro {
    align-items: center;
  }
}

.lista {
  display: flex;
  flex-direction: column;
  margin-bottom: 3em;
}
@media (min-width: 1024px) {
  .lista {
    width: 70%;
    flex-direction: row;
    justify-content: center;
    border-bottom: 0.2px solid #9B9CA1;
  }
}
.lista .btn {
  padding: 1.6em 0;
  background: transparent;
  border: none;
  border-bottom: 0.3px solid #9B9CA1;
  text-transform: capitalize;
  color: #9B9CA1;
  font-weight: bold;
  font-size: 1.2em;
  text-align: center;
  position: relative;
  display: block;
  transition: 1s;
}
@media (min-width: 1024px) {
  .lista .btn {
    border-bottom: none;
    margin-left: 5em;
    width: 33.3%;
  }
}
.lista .btn:nth-child(1) {
  border-top: 0.3px solid #9B9CA1;
}
@media (min-width: 1024px) {
  .lista .btn:nth-child(1) {
    border-top: none;
    margin-left: 0em;
  }
}
.lista .btn.active {
  color: #242A42;
  transition: 1.5s;
}
.lista .btn.active::before {
  content: "";
  position: absolute;
  height: 0.2em;
  width: 50%;
  background-color: #ff5657;
  bottom: 0;
  left: 0;
  z-index: 2;
  transform: translateX(50%);
  border-radius: 0.1em;
}

.contenidoLista {
  margin-top: 3em;
  width: 100%;
}
@media (min-width: 1024px) {
  .contenidoLista {
    display: flex;
    justify-content: center;
    margin-top: 1em;
  }
}
.contenidoLista .contenido {
  display: none;
  height: 50vh;
  position: relative;
  transition: 1s;
}
.contenidoLista .contenido::before {
  content: "";
  position: absolute;
  width: 85%;
  height: 60%;
  background-color: #5266DF;
  bottom: 0;
  border-radius: 0 40% 30% 0;
  left: 0;
  transform: translate(-5%, -20%);
  z-index: -1;
}
@media (min-width: 1024px) {
  .contenidoLista .contenido::before {
    width: 35%;
    height: 90%;
    transform: translate(-5%, 20%);
  }
}
.contenidoLista .contenido .image {
  height: 100%;
}
@media (min-width: 1024px) {
  .contenidoLista .contenido .image {
    width: 30%;
  }
}
.contenidoLista .contenido img {
  max-width: 80%;
  height: 70%;
}
@media (min-width: 1024px) {
  .contenidoLista .contenido img {
    max-width: 100%;
    height: 90%;
  }
}
.contenidoLista .contenido .info {
  margin-top: 7em;
}
@media (min-width: 1024px) {
  .contenidoLista .contenido .info {
    margin-top: 0;
    width: 40%;
    text-align: start;
    margin-left: 15em;
  }
}
@media (min-width: 1024px) {
  .contenidoLista .contenido .info .descripcion {
    width: 70%;
  }
}
@media (min-width: 1024px) {
  .contenidoLista .contenido .info a {
    padding: 1em 2.7em;
    background-color: #5266DF;
    border-radius: 0.4em;
    color: #FFF;
    font-weight: bold;
    width: 90%;
    -webkit-box-shadow: -3px 6px 9px -3px #9b9ca1;
    -moz-box-shadow: -3px 6px 9px -3px #9b9ca1;
    box-shadow: -3px 6px 9px -3px #9b9ca1;
    text-transform: capitalize;
    margin: 3em 0;
  }
}

.contenido.active {
  display: block;
}
@media (min-width: 1024px) {
  .contenido.active {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
}

.download {
  text-align: center;
  margin-top: 25em;
  padding-top: 5em;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .download {
    margin-top: 0;
  }
}
.download .titulo {
  text-transform: none;
  margin-bottom: 1em;
}
.download .descripcion {
  width: 78%;
}
@media (min-width: 1024px) {
  .download .descripcion {
    width: 30%;
  }
}

.cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  align-items: center;
}
@media (min-width: 1024px) {
  .cards {
    flex-direction: row;
  }
}
.cards .card {
  width: 90%;
  -webkit-box-shadow: 0px 12px 23px -6px #9b9ca1;
  -moz-box-shadow: 0px 12px 23px -6px #9b9ca1;
  box-shadow: 0px 12px 23px -6px #9b9ca1;
  margin-bottom: 4em;
  border-radius: 2em;
  padding: 2.5em 0em;
  padding-bottom: 2em;
  animation: 1s cards infinite;
}
@media (min-width: 1024px) {
  .cards .card {
    max-width: 18%;
    margin-left: 2em;
  }
  .cards .card:nth-child(1) {
    margin-left: 0;
  }
  .cards .card:nth-child(2) {
    transform: translateY(30%);
  }
  .cards .card:nth-child(3) {
    transform: translateY(60%);
  }
}
.cards .card .titulo {
  margin-bottom: 0em;
  padding: 0em 1em;
}
@media (min-width: 1024px) {
  .cards .card .titulo {
    font-size: 1.8em;
  }
}
.cards .card .descripcion {
  width: 100%;
}
@media (min-width: 1024px) {
  .cards .card .descripcion {
    font-size: 0.9em;
  }
}
.cards .card .button {
  margin-top: 2em;
  border-top: 6px dotted #9B9CA1;
  padding-top: 2em;
}
.cards .card .button a {
  margin: 3em 0;
}

.preguntas {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.preguntas .descripcion {
  width: 85%;
}
@media (min-width: 1024px) {
  .preguntas .descripcion {
    width: 30%;
  }
}
@media (min-width: 1024px) {
  .preguntas {
    margin-top: 10em;
  }
}

.accordion {
  max-width: 100%;
}
@media (min-width: 1024px) {
  .accordion {
    width: 30%;
  }
}
.accordion .contenidoAccordion {
  width: 90%;
  position: relative;
  margin: 0 auto;
}
.accordion .contenidoAccordion .elementoAccordion {
  position: relative;
  padding: 1em;
  text-align: start;
  border-bottom: 1px solid #9B9CA1;
  color: #242A42;
  cursor: pointer;
}
.accordion .contenidoAccordion .elementoAccordion::before {
  content: "";
  background-image: url("/build/img/icon-arrow.svg");
  background-repeat: no-repeat;
  position: absolute;
  width: 1em;
  height: 1em;
  top: 50%;
  right: 5%;
  transform: translateY(-50%);
  transition: 1s;
}
.accordion .contenidoAccordion .contenidoElemento {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: 1.5s;
}
.accordion .contenidoAccordion .contenidoElemento p {
  font-size: 1em;
  color: #9B9CA1;
}
.accordion .contenidoAccordion.active .elementoAccordion {
  border-bottom: none;
  color: #ff5657;
  font-weight: bold;
}
.accordion .contenidoAccordion.active .elementoAccordion::before {
  content: "";
  background-image: url("/build/img/icon-arrow.svg");
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  right: 5%;
  transform: translateY(-50%) rotate(180deg);
  filter: invert(100%);
}
.accordion .contenidoAccordion.active .contenidoElemento {
  height: 18em;
}
@media (min-width: 1024px) {
  .accordion .contenidoAccordion.active .contenidoElemento {
    height: 15em;
  }
}

.contactUs {
  max-width: 100%;
  text-align: center;
  background: #5266DF;
  min-height: 50%;
  padding: 4em 0;
  margin-top: 10em;
  color: #FFF;
  font-weight: bold;
  text-transform: capitalize;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 5em;
}
.contactUs span {
  letter-spacing: 0.1em;
}
.contactUs p {
  width: 100%;
  font-size: 1.7em;
  line-height: 1.2;
}
@media (min-width: 1024px) {
  .contactUs p {
    margin-bottom: 0;
  }
}
.contactUs form {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
@media (min-width: 1024px) {
  .contactUs form {
    display: block;
  }
}
.contactUs form input {
  width: 90%;
  padding: 1.2em 1em;
  background: #FFF;
  outline: none;
  border: none;
  border-radius: 0.5em;
  margin-top: 3em;
  margin-bottom: 2em;
  position: relative;
  z-index: 2;
}
@media (min-width: 1024px) {
  .contactUs form input {
    width: 70%;
  }
}
.contactUs form .buttonEmail {
  padding: 1.2em 1em;
  background-color: #ff5657;
  border-radius: 0.4em;
  color: #FFF;
  font-weight: bold;
  width: 90%;
  -webkit-box-shadow: -3px 6px 9px -3px #9b9ca1;
  -moz-box-shadow: -3px 6px 9px -3px #9b9ca1;
  box-shadow: -3px 6px 9px -3px #9b9ca1;
  text-transform: capitalize;
  margin-top: 1em;
}
@media (min-width: 1024px) {
  .contactUs form .buttonEmail {
    width: 25%;
    margin-left: 1em;
    transition: 0.6s;
  }
  .contactUs form .buttonEmail:hover, .contactUs form .buttonEmail:active {
    background: #FFF;
    color: #ff5657;
    border: 2px solid #ff5657;
  }
}

.error {
  position: absolute;
  font-size: 0.9em;
  background: #ff5657;
  padding: 0 1em;
  border-radius: 0.5em 0.5em 0.5em 0;
  opacity: 0;
  left: 5%;
  top: 42%;
  height: 2em;
}
@media (min-width: 1024px) {
  .error {
    top: 0;
    left: 0;
    bottom: 0;
    transform: translate(1%, 75%);
  }
}

.error.wrong {
  opacity: 1;
}

.inputEmail.wrong {
  border: 2px solid #ff5657;
}

.success {
  position: absolute;
  font-size: 0.9em;
  background: #242946;
  padding: 0 1em;
  border-radius: 0.5em 0.5em 0.5em 0;
  opacity: 0;
  left: 5%;
  top: 42%;
  height: 2em;
}
@media (min-width: 1024px) {
  .success {
    top: 0;
    left: 0;
    bottom: 0;
    transform: translate(1%, 75%);
  }
}

.success.great {
  opacity: 1;
}

.inputEmail.great {
  border: 2px solid #242946;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 4em 0;
  background: #242946;
}
@media (min-width: 1024px) {
  .footer {
    flex-direction: row;
    justify-content: space-between;
    padding: 2em 4em;
    align-items: center;
  }
}
.footer .logo {
  width: 70%;
  margin-bottom: 2em;
}
@media (min-width: 1024px) {
  .footer .logo {
    margin-bottom: 0;
    width: 20%;
  }
}
.footer .menu {
  line-height: 5em;
}
@media (min-width: 1024px) {
  .footer .menu {
    display: flex;
    justify-content: flex-end;
    width: 60%;
  }
}
.footer .menu li {
  text-align: center;
}
@media (min-width: 1024px) {
  .footer .menu li {
    margin-left: 4em;
  }
  .footer .menu li:nth-child(1) {
    margin-left: 0;
  }
}
.footer .menu li a {
  color: #9B9CA1;
  font-size: 1.5em;
  text-transform: uppercase;
  transition: 1s;
}
.footer .menu li a:hover {
  color: #ff5657;
}
.footer .social {
  display: flex;
  align-items: flex-end;
  margin-top: 5%;
}
@media (min-width: 1024px) {
  .footer .social {
    margin-top: 0;
  }
}
.footer .social li {
  border: none;
  margin: 0 2em;
  cursor: pointer;
}
.footer .social li i {
  color: #FFF;
  font-size: 2em;
  transition: 1s;
}
.footer .social li i:hover {
  color: #ff5657;
}
@media (min-width: 1024px) {
  .footer .social li {
    margin: 0 1em;
  }
}